@font-face {
  font-family: appNameFont;
  src: url("../../resources/fonts/HATINH.TTF"); }

@font-face {
  font-family: appDescriptionFont;
  src: url("../../resources/fonts/Nunito-Light.ttf"); }

@font-face {
  font-family: appFooterFont;
  src: url("../../resources/fonts/HelveticaNeue-Light.otf"); }

.container-fluid {
  padding: 0; }
  .container-fluid .app-header {
    height: 10vh;
    font-size: 16px;
    box-shadow: 0 4px 8px 0 rgba(255, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 255, 0, 0.2); }
    .container-fluid .app-header .app-name {
      height: 66%;
      display: flex;
      justify-content: center;
      align-items: center; }
      .container-fluid .app-header .app-name p {
        font-family: appNameFont, sans-serif;
        font-size: 3rem;
        margin: 0; }
    .container-fluid .app-header .app-description {
      height: 33%;
      display: flex;
      justify-content: center;
      align-items: center; }
      .container-fluid .app-header .app-description p {
        font-family: appDescriptionFont, sans-serif;
        font-size: 1.75rem;
        margin: 0; }
  .container-fluid .app-body {
    height: 85vh;
    display: flex;
    border-left: 1px solid #e4e4e4;
    border-right: 1px solid #e4e4e4; }
    .container-fluid .app-body .app-controller {
      display: block;
      width: 380px;
      min-width: 300px;
      border-right: 1px solid #e4e4e4; }
      .container-fluid .app-body .app-controller .accordion .card .card-body {
        max-height: calc(85vh - 60px - 60px);
        overflow-y: scroll;
        scroll-behavior: smooth;
        scrollbar-color: transparent;
        padding: 0 0.6rem; }
        .container-fluid .app-body .app-controller .accordion .card .card-body > div {
          padding-bottom: 0.5rem;
          padding-top: 0.5rem; }
          .container-fluid .app-body .app-controller .accordion .card .card-body > div .add-row-container {
            cursor: pointer;
            margin-top: 10px;
            display: flex;
            color: #757575;
            font-size: 1rem;
            align-items: center; }
            .container-fluid .app-body .app-controller .accordion .card .card-body > div .add-row-container > p {
              margin: 0 0 0 8px; }
      .container-fluid .app-body .app-controller .accordion .card .card-header {
        height: 60px;
        padding: 0.75rem 0.5rem 0.75rem 1rem; }
      .container-fluid .app-body .app-controller .accordion .card .card-body::-webkit-scrollbar {
        display: none; }
      .container-fluid .app-body .app-controller .app-controller-nav {
        margin-bottom: 20px; }
        .container-fluid .app-body .app-controller .app-controller-nav button {
          width: 100%; }
      .container-fluid .app-body .app-controller .left-panel-tab {
        height: 50px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-right: 0.1rem; }
        .container-fluid .app-body .app-controller .left-panel-tab .icon-container {
          background-color: transparent;
          border-color: transparent;
          box-shadow: none !important;
          display: flex;
          justify-content: center; }
        .container-fluid .app-body .app-controller .left-panel-tab p {
          font-family: appDescriptionFont, sans-serif;
          font-size: 1.25rem;
          font-weight: bold;
          margin: 0; }
    .container-fluid .app-body .app-drawing-panel {
      display: flex;
      flex: 1;
      margin: 10px 0 10px 0; }
  .container-fluid .app-footer {
    position: relative;
    background-color: #fff;
    display: flex;
    height: 5vh;
    justify-content: center;
    align-items: center;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2); }
    .container-fluid .app-footer p {
      font-family: appFooterFont, sans-serif;
      font-size: 1rem;
      margin: 0; }
  .container-fluid #tooltip-right .tooltip-inner {
    background-color: rgba(0, 0, 0, 0.8);
    color: #fff;
    line-break: normal;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif, sans-serif;
    font-size: 1rem;
    line-height: 1rem;
    text-align: left;
    white-space: pre;
    max-width: none; }
  .container-fluid #tooltip-right .arrow:before {
    border-top: 5px solid transparent;
    border-bottom: 5px solid transparent;
    border-right: 5px solid #fff; }
