@font-face {
  font-family: menuFont;
  src: url(/gemath/static/media/HelveticaNeue-Light.35321a78.otf); }

.input-item {
  height: 36px;
  border-radius: 3px;
  margin-bottom: 1rem; }
  .input-item .context-menu {
    position: fixed;
    z-index: 9999;
    background-color: white;
    border: 1px solid #dddddd;
    border-radius: 3px; }
    .input-item .context-menu .context-menu-item {
      height: 40px;
      display: flex;
      flex-direction: row;
      padding: 0 5px;
      align-items: center; }
      .input-item .context-menu .context-menu-item p {
        margin-top: auto;
        margin-bottom: auto;
        margin-left: 5px;
        font-family: menuFont, sans-serif;
        font-size: 1rem; }
  .input-item .input-container {
    width: 100%;
    position: relative; }
    .input-item .input-container .input-remove-btn {
      z-index: 1000;
      width: 0px;
      height: 0px;
      display: flex; }
    .input-item .input-container input {
      display: flex;
      flex: 1 1;
      box-shadow: none; }
    .input-item .input-container input:focus {
      box-shadow: none;
      -moz-box-shadow: none;
      -webkit-box-shadow: none; }
    .input-item .input-container .input-status span {
      background-color: white; }
  .input-item .input-container:hover .input-remove-btn {
    position: absolute;
    right: 2px;
    top: 2px;
    width: 15px;
    height: 15px;
    justify-content: center;
    align-items: center; }

.success {
  border: solid 1px #28a745 !important; }

.error {
  border: solid 1px #dc3545 !important; }

.normal {
  border: solid 1px #6c757d !important; }



